import React, { Fragment } from 'react'
import Navbar from '../components/Navbar'
import Footer from '../components/Footer'
import { useSelector } from 'react-redux'
import { isAuthenticated } from './auth'
import { useNavigate } from 'react-router-dom'

const ConfirmOrder = () => {
    const navigate = useNavigate()

    const { cartItems, shippingInfo } = useSelector(state => state.cart)
    const { user } = isAuthenticated()
    const totalPrice = cartItems.reduce((ac, item) => (ac + item.quantity * item.price), 0)

    const processToPayment = () => {
        const data = {
            totalPrice
        }
        sessionStorage.setItem('orderInfo', JSON.stringify(data))
        navigate('/payment')
    }

    return (
        <>
            <Navbar />
            <div className='container-fluid my-5'>
                <div className='row d-flex justify-content-center mb-3'>
                    <div className="col-12 col-md-3 py-3">
                        <div className="card">
                            <h2 className='card-header bg-warning text-center'>Shipping Info</h2>
                            <div className='card-body'>
                                <div className='py-1'>
                                    <b>Name</b>:
                                    <span className='text-muted'>
                                        {user.name}
                                    </span>
                                </div>
                                <div  className='py-1'>
                                    <b>Email</b>:
                                    <span className='text-muted'>
                                        {user.email}
                                    </span>
                                </div>
                                <div  className='py-1'>
                                    <b>City</b>:
                                    <span className='text-muted'>
                                        {shippingInfo.city}
                                    </span>
                                </div>
                                <div  className='py-1'>
                                    <b>Phone Number</b>:
                                    <span className='text-muted'>
                                        {shippingInfo.phone}
                                    </span>
                                </div>
                                <div  className='py-1'>
                                    <b>Country</b>:
                                    <span className='text-muted'>
                                        {shippingInfo.country}
                                    </span>
                                </div>
                                <div  className='py-1'> 
                                <b>Shipping Address</b>:
                                <span className='text-muted'>
                                    {shippingInfo.shippingAddress1},
                                    {shippingInfo.shippingAddress2}
                                </span>
                            </div>
                            </div>
                            
                        </div>
                        
                    </div>
                    <div className='col-md-6 py-3'>
                        <div className="card">
                        <h2 className=' card-header bg-danger text-center'>Your Cart Items</h2>
                        <div className="card-body">
                        {cartItems.map((item, i) => (
                            <Fragment key={i}>
                                
                                <div className='row align-items-center justify-content-center'>
                                    <div className='col-md-3'>
                                        <img src={`http://localhost:5000/${item.image}`} alt={item.name} width="100" />
                                    </div>
                                    <div className='col-md-3'>
                                        <p className='text-muted'>
                                            {item.name}
                                        </p>
                                    </div>
                                    <div className='col-md-3'>
                                        <p className='text-warning'>
                                            Rs.{item.price}x{item.quantity}=<b>Rs.{item.price * item.quantity}</b>
                                        </p>

                                    </div>
                                    
                                </div>
                                <hr />

                            </Fragment>
                        ))}
                        </div>
                    </div>
                    </div>
                    <div className='col-md-3 py-3'>
                        <div className=' card '>
                            <h2 className="card-header bg-warning text-center">Order Summary</h2>
                            <div className="card-body text-center">
                            <p>SubTotal : <span>
                                {cartItems.reduce((ac, item) => (ac + Number(item.quantity)), 0)} (Units)
                            </span></p>
                            
                            <p>TotalPrice : Rs.<span>{totalPrice}</span></p>
                            
                            <button className='btn btn-warning'
                                onClick={processToPayment}
                            >
                                Proceed To Payment
                            </button>
                            </div>
                            
                        </div>
                    </div>
                </div>
            </div>
            <Footer />

        </>
    )
}

export default ConfirmOrder
